import React, { PropsWithChildren } from "react"
import { Box } from "@rp/react/web/mui/core"

type Props = PropsWithChildren<{
  header?: React.ReactNode
  chat?: React.ReactNode
  suggestions?: React.ReactNode
  chatWidth?: number
  isChatCollapsed?: boolean
}>

const DASHBOARD_CONTAINER_CLASS_NAME = "dashboard-container"

const DashboardContainer = ({
  header,
  chat,
  children,
  suggestions,
  chatWidth = 500,
  isChatCollapsed = false,
}: Props) => {
  const tableWidth = isChatCollapsed ? "100%" : `calc(100% - ${chatWidth}px)`

  return (
    <Box
      className={DASHBOARD_CONTAINER_CLASS_NAME}
      sx={{ display: "flex", gap: isChatCollapsed ? 0 : 3, pt: 2, paddingBottom: 2 }}
    >
      <Box
        sx={{
          width: tableWidth,
          minWidth: 0,
          transition: "width 0.2s ease-in-out",
        }}
      >
        <Box sx={{ height: "100%", display: "flex", flexDirection: "column" }}>
          {header}
          <Box sx={{ flexGrow: 1, display: "flex", flexDirection: "column" }}>
            <Box component="section" sx={{ minHeight: "calc(100dvh - 608px)" }}>
              {children}
            </Box>
            {suggestions}
          </Box>
        </Box>
      </Box>
      <Box
        sx={{
          width: isChatCollapsed ? 0 : chatWidth,
          maxWidth: chatWidth,
          position: "relative",
          visibility: isChatCollapsed ? "hidden" : "visible",
          transition: "0.2s ease-in-out",
        }}
      >
        {chat}
      </Box>
    </Box>
  )
}

export { DashboardContainer, DASHBOARD_CONTAINER_CLASS_NAME }
